.bwd-icon-picker {
    position: relative;
    .icon-list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 10px;
        margin-top: 20px;
        position: absolute;
        left: 0;
        top: 55px;
        width: 100%;
        z-index: 999;
        background: #fff;
        height: 200px;
        overflow: auto;
        .icon {
            padding: 0;
            height: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #0000;
            border: 1px solid #ddd;
            cursor: pointer;
            &.active-icon {
                border-color: #4c93ba !important;
                svg {
                    fill: #4c93ba; 
                }
            }
            svg {
                width: 18px;
                height: 18px;
            }
        }
    }
    .bwd-icon-header {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        background: #fff;
        border: 1px solid #ccc;
        padding: 0;
        position: relative;
        .bwd-selected-icon {
            line-height: 1;
            padding: 5px;
             svg {
                width: 18px;
                height: 18px;
            }
        }
        .bwd-open-icon {
            background: #cbcbcb;
            cursor: pointer;
            height: 100%;
            display: flex;
            position: absolute;
            right: 0;
            align-items: center;
            padding: 5px;
        }
    }
}

